<template>
  <div class="home">
    <transition name="fade">
      <swiper id="swiperBox" v-bind:options="swiperOption" ref="mySwiper">
        <swiper-slide class="swiper-slide1">
          <div class="page">
            <div class="cinema" @click="openCinema">
              <img src="../assets/4DCinema.png">
              <p class="areaText">4D影院</p>
            </div>
            <div class="lifeScience" @click="openScience">
              <img src="../assets/lifeScience.png">
              <p class="areaText">生命科学展区</p>
            </div>
            <div class="spaceExplore" @click="openExplore">
              <img src="../assets/spaceExplore.png">
              <p class="areaText">宇宙探索展区</p>
            </div>
            <div class="robot" @click="openRobot">
              <img src="../assets/robot.png">
              <p class="areaText">机器人世界展区</p>
            </div>
            <div class="information" @click="openInformation">
              <img src="../assets/information.png">
              <p class="areaText">信息万象城市之光展区</p>
            </div>
          </div>
        </swiper-slide>
        <!-- 第二页 -->
        <swiper-slide class="swiper-slide2">
          <div class="page">
            <div class="basicScience" @click="openBasic">
              <img src="../assets/basicScience.png">
              <p class="areaText">基础科学展区</p>
            </div>
            <div class="publicSafety" @click="openSafety">
              <img src="../assets/publicSafety.png">
              <p class="areaText">公共安全展区</p>
            </div>
            <div class="childPlace" @click="openPlace">
              <img src="../assets/childPlace.png">
              <p class="areaText">少儿天地展区</p>
            </div>
            <p class="areaText imagin">童心创想长廊</p>
            <p class="areaText report">报告厅</p>
          </div>
        </swiper-slide>
      </swiper>
    </transition>
  </div>
</template>
<script>
import { swiper, swiperSlide } from 'vue-awesome-swiper'
export default {
  components: {
    swiper,
    swiperSlide
  },
  data () {
    return {
      swiperOption: {
        // swiper configs 所有的配置同swiper官方api配置
        notNextTick: true, // notNextTick是一个组件自有属性，如果notNextTick设置为true，组件则不会通过NextTick来实例化swiper，也就意味着你可以在第一时间获取到swiper对象，假如你需要刚加载遍使用获取swiper对象来做什么事，那么这个属性一定要是true
        direction: 'vertical', // 水平方向移动
        grabCursor: true, // 鼠标覆盖Swiper时指针会变成手掌形状，拖动时指针会变成抓手形状
        setWrapperSize: true, // Swiper使用flexbox布局(display: flex)，开启这个设定会在Wrapper上添加等于slides相加的宽或高，在对flexbox布局的支持不是很好的浏览器中可能需要用到。
        autoHeight: true, // 自动高度。设置为true时，wrapper和container会随着当前slide的高度而发生变化
        slidesPerView: 1, // 设置slider容器能够同时显示的slides数量(carousel模式)。可以设置为数字（可为小数，小数不可loop），或者 'auto'则自动根据slides的宽度来设定数量。loop模式下如果设置为'auto'还需要设置另外一个参数loopedSlides。
        mousewheel: false, // 开启鼠标滚轮控制Swiper切换。可设置鼠标选项，默认值false
        mousewheelControl: false, // 同上
        height: window.innerHeight, // 高度设置，占满设备高度
        resistanceRatio: 0, // 抵抗率。边缘抵抗力的大小比例。值越小抵抗越大越难将slide拖离边缘，0时完全无法拖离。本业务需要
        observeParents: true, // 将observe应用于Swiper的父元素。当Swiper的父元素变化时，例如window.resize，Swiper更新
        // 如果自行设计了插件，那么插件的一些配置相关参数，也应该出现在这个对象中，如下debugger
        debugger: true,
        // swiper的各种回调函数也可以出现在这个对象中，和swiper官方一样
        onTransitionStart (swiper) {
          console.log(swiper)
        },
        on: {
          // 监听滑动切换事件，返回swiper对象
          slideChange: () => {
            let swiper = this.$refs.mySwiper.swiper
            console.log(swiper.activeIndex) // 滑动打印当前索引
          }
        }
      }
    }
  },
  computed: {
    swiper () {
      return this.$refs.mySwiper.swiper
    }
  },
  methods: {
  },
  mounted () {
    console.log('this is current swiper instance object', this.swiper)
    this.swiper.slideTo(0, 1000, false) // 初始滑动到哪个页面
  }
}
</script>

<style scope>
.fade-enter-active, .fade-leave-active {
  transition: opacity .5s;
}
.fade-enter, .fade-leave-to{
  opacity: 0;
}
.swiper-slide1{
  background: url('../assets/firstFloor.jpg');
  /* background-position: center center;
  background-repeat: no-repeat;
  background-attachment: fixed; */
  background-size: 100% 100%;
}
.swiper-slide2{
  background: url('../assets/secondFloor.jpg');
  background-size: 100% 100%;
}
.areaText{
  font-size:8px;
  color: #ffffff;
}
.cinema{
  position: absolute;
  left: 19%;
  top: 16%;
}
.cinema img{
  width: 60px;
  height: 60px;
}
.lifeScience{
  position: absolute;
  left: 66%;
  top: 25%;
}
.lifeScience img{
  width: 65px;
  height: 57px;
}
.spaceExplore{
  position: absolute;
  left: 39%;
  top: 40%;
}
.spaceExplore img{
  width: 66px;
  height: 60px;
}
.robot{
  position: absolute;
  left: 64.5%;
  top: 48%;
}
.robot img{
  width: 65px;
  height: 57px;
}
.information{
  position: absolute;
  left: 32%;
  top: 73%;
}
.information img{
  width: 62px;
  height: 54px;
}
.basicScience{
  position: absolute;
  left: 64%;
  top: 35%;
}
.basicScience img{
  width: 59px;
  height: 52px;
}
.publicSafety{
  position: absolute;
  left: 36%;
  top: 52%;
}
.publicSafety img{
  width: 55px;
  height: 50px;
}
.childPlace{
  position: absolute;
  left: 12.3%;
  top: 76%;
}
.childPlace img{
  width: 45px;
  height: 40px;
}
.imagin{
  position: absolute;
  left: 57%;
  top: 72%;
}
.report{
  position: absolute;
  left: 71%;
  top: 79%;
}
</style>



